



























































import { Component, Vue, Prop, Watch } from 'vue-property-decorator'
import houseSelect from '@/components/common-components/houseSelect'
import Page from '@/components/common-components/page.vue'
import wx from 'weixin-js-sdk'
import { isHasFunc } from '@/until/index'

@Component({
  components: {
    houseSelect,
    Page
  }
})
export default class Home extends Vue {
  $createDialog : any
  // 声明周期钩子
  model = { inputValue: '', pcaValue: [], dateValue: '' }
  fields =[{ type: 'input', modelKey: 'inputValue', label: 'Input', props: { placeholder: '请输入' }, rules: { required: true } }]
  valid = undefined
  validity = {}
  // 小区选择数据
  houseId = ''
  accountInfo = {}
  showSelect = false
  title = '房屋选择'
  placeholder = '请选择房屋'
  mounted () {
    // this.pageInit()
  }

  activated () {
    // this.pageInit()
  }

  @Prop({ default: 'default value' }) readonly propB!: string

  @Watch('$route')
  onRouterChanged () {
    // this.getWeiXinConfig()
  }

  validateHandler (result : any) {
    this.validity = result.validity
    this.valid = result.valid
    console.log('validity', result.validity, result.valid, result.dirty, result.firstInvalidFieldIndex)
  }
  submitHandler (e : any) {
    console.log('submit')
  }
  goTo (type : any) {
    this.$router.push({ path: '/' + type })
  }
  scanQRCode () {
    let that = this
    wx.scanQRCode({
      needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
      scanType: ["qrCode","barCode"], // 可以指定扫二维码还是一维码，默认二者都有
      success: function (res : any) {
        var result = res.resultStr // 当needResult 为 1 时，扫码返回的结果
        that.$store.dispatch('qrValidate', {qrId: result}).then((res)=>{
          if (res && res.success) {
            that.$createDialog({
              type: 'alert',
              title: '核销成功',
              content: '核销成功',
              icon: 'cubeic-right'
            }).show()
          } else {
             that.$createDialog({
              type: 'alert',
              title: '核销失败',
              content: '此码无效！',
              icon: 'cubeic-wrong'
            }).show()
          }
        })
      }
    })
  }
  getWeiXinConfig () {
    this.$store.dispatch('getWeixinConfig', { user: '13934691550', pass: 'wangtao' }).then((res)=>{
      console.log('获取微信配置成功', res)
      if (res.success) {
        let config = res.data
        wx.config({
          debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
          appId: 'wxc6c897a704d3fac4', // 必填，公众号的唯一标识
          timestamp: config.timestamp, // 必填，生成签名的时间戳
          nonceStr: config.nonceStr, // 必填，生成签名的随机串
          signature: config.signature,// 必填，签名
          jsApiList: ['scanQRCode'] // 必填，需要使用的JS接口列表
        });
      }
      wx.ready(function() {
        console.log('ready========')
      })
      wx.error(function(err:any) {
        console.log(err)
        console.log('errrrrrrrrrrrrrr')
      })
    })
  }

  toOtherPage (type: any) {
    if (type) {
      let path = '/' + type
      let query = {}
      if (this.houseId) {
        query.houseId = this.houseId
      }
      this.$router.push({ path: path, query: query })
    }
  }

  getPower (key: any) {
    let user = this.$store.state.userInfo
    return isHasFunc(key, user)
  }

  getHouseInfo () {
    let info = {}
    this.$store.state.houseList.forEach((obj) =>{
      if (obj.id === this.houseId) {
        info = obj
      }
    })
    return info
  }

  setHouseId (id:any) {
    this.houseId = id
  }

  get showAdmin () {
    let houseInfo = this.getHouseInfo()
    if (this.$store.state.userInfo && this.$store.state.userInfo.power && this.$store.state.userInfo.power.length > 0) {
      return this.$store.state.userInfo.adminAccount.indexOf(houseInfo.accountId) > -1
    } else {
      return false
    }
  }
  get showToAdminBtn () {
    return this.$store.state.userInfo && this.$store.state.userInfo.power && this.$store.state.userInfo.power.length > 0
  }
  get showUserHandle () {
    return this.$store.state.userInfo && this.$store.state.userInfo.accountId && this.$store.state.userInfo.accountId.length > 0
  }
}
